body {
  font-size: medium;
  font-family: Arial, Helvetica, sans-serif;
  margin: 20px;
}

.header {
  text-align: center;
}

.game-board {
  margin-left: auto;
  margin-right: auto;
  width: 360px;
}
  
.board-row:after {
  clear: both;
  content: "";
  display: table;
}
  
.status {
  text-align: center;
  margin-bottom: 10px;
}
  
.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  height: 120px;
  width: 120px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.square-image {
  height: 120px;
  width: 120px;
}

.bottom-text {
  text-align: center;
}
